.wprf-control .wprf-input-label {
  display: block;
  padding: 5px;
  margin-bottom: 10px;
}
.wprf-control .wprf-textarea-field {
  display: block;
  width: 100%;
  box-shadow: none;
  border: 1px solid #f2f2f2;
  transition: border linear 0.2s, box-shadow linear 0.2s;
  padding: 8px;
  line-height: 1;
  font-size: 14px;
}